import React from 'react'
import { useState } from 'react';
import './../App.css';
function Projet({titre,image,type, texte,skills}) {
  const [style, updateStyle] = useState("Projet");
  return (
    <div className={style!="Projet"?"ProjectBackground":"ProjetContainer"} onClick={()=>{
      if (style != "Projet") {
        updateStyle('Projet')
      }
    }}>
        <div className={style} onClick={()=>{
          updateStyle("ProjetBigSurface")
        }}>
          <div className='projectColumn'>
              <img src={image} className="imgProject"/> 
              <p className='ProjetTitre'>{titre}</p>
              <p className='ProjetType'>{type}</p>  
              <p className="ProjectDescription">{texte}</p>
              <p className="ProjetTechno">{skills}</p>
            </div>
        </div>
      </div>
  );
}
export default Projet;
