import React from 'react'
import './../App.css';
import Arrow from './Arrow';
import Me from './../images/Me.png';
import Soleil from './soleil';
import bird from './../images/bird.gif'
import {useEffect, useState} from "react";
function Mateo() {
  const [x, updateX] = useState()
  function parallax(event) {
    document.querySelectorAll(".parallax-wrap span").forEach((shift) => {
      const position = shift.getAttribute("value");
      const x = (window.innerWidth - event.pageX * position) / 90;
      const y = (window.innerHeight - event.pageY * position) / 90;
      shift.style.transform = `translateX(${x}px) translateY(${y}px)`;
    });
  } 
  return (
    <div className="Mateo" id='Mateo' onMouseMove={(e)=>{
      parallax(e)
    }}>
        <img src={Me} className="Me"/>
        <h1 className='CouleurTexte'>Mat√©o Boukhobza</h1>
        <h5>Portefolio en cours d'am√©lioration</h5>
        <div className="blue parallax-wrap"></div>
        <div className="red parallax-wrap"></div>
        <div className="Orange parallax-wrap"></div>
        <div className={{backgroundColor:"red",height:"30px", width:"30px"}}></div>
      <Arrow selection="Agilit√©"/>
    </div>
  );
}
export default Mateo;
